<template>
  <div class="shoesList">
    <div class="main" :key="index" v-for="(m,index) in conList.mainList">
      <div class="mainCon">
        <img :src="m.img1" alt="">
        <div class="shotCon">
          <p class="date">{{m.date}}</p>
          <p>{{m.pCon1}}</p>
          <p>{{m.pCon2}}</p>
          <code class="small">{{m.small1}}<img  :src="m.img2" alt=""></code>
          <div class="recmend">
            <span>{{m.span1}}</span>
            <span>{{m.span2}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "FashionConList",
        props:["conList"]
    }
</script>

<style scoped>
  .shoesList{
    width: 100%;
    background-color: #fff;
  }
  .main .mainCon{
    width: 3.37rem;
    height: 1.82rem;
    border-bottom: 1px solid #eee;
    display: flex;
    justify-content: space-around;
    margin-left: .17rem;
  }
  .main img{
    width: 1.68rem;
    height: 1.65rem;
    margin-top: .16rem;
  }
  .main  .shotCon{
    display: flex;
    justify-content: left;
    flex-direction: column;
    width: 1.87rem;
    margin-top: .16rem;
  }
  .main  .shotCon .date{
    color: #959595;
    font-size: .12rem;
    line-height: .3rem;
  }
  .main  .shotCon p{
    color: #000;
    font-size: .19rem;
    line-height: .32rem;
  }
  .main  .shotCon .small{
    font-size: .15rem;
    color: #919191!important;
    display: flex;
    margin-top: .05rem;
  }
  .main  .shotCon .small img{
    width: .08rem;
    height: .035rem;
    margin-top: .05rem;
    margin-left: .04rem;
  }
  .main  .shotCon .recmend{
    display: flex;
    justify-content: left;
  }
  .main  .shotCon .recmend span{
    color: #9d9d9d;
    font-size: .12rem;
    margin-top: .12rem;
  }
  .main  .shotCon .recmend span:last-child{
    margin-left: .05rem;
  }
  .main  .shotCon  .Limit .timeLinit{
    width: .25rem;
    height: .16rem;
    line-height: .16rem;
    text-align: center;
    border: 1px solid #ab7045;
    border-radius: 3px;
    color: #ab7045;
    font-size: .07rem;
    margin-top: .12rem;
    margin-right: .05rem;
  }
</style>



