<template>
    <div class="list">
        <!--shopBar-->
        <div class="shopBar">
          <div class="shopLogo">
            <img class="logoimgg" :src="bar.img1" alt="">
            <div class="shopInfo">
              <div class="row">
                <img  :src="bar.img2" alt="">
                {{bar.store}}
              </div>
              <code class="limitTime">{{bar.limittime}}<img  :src="bar.img3" alt=""></code>
            </div>
          </div>
          <img class="jt"  :src="bar.img4" alt="">
        </div>
        <div class="reconmend">{{bar.reconmend}}</div>
    </div>
</template>

<script>
    export default {
        name: "GoodsShopBar",
        props:["bar"]
    }
</script>

<style scoped>
  /*shopBar*/
  .shopBar{
    width: 100%;
    margin-top: .1rem;
    height: .75rem;
    background-color: #fff;
    text-align: center;
    position: relative;
  }
  .shopBar .jt{
    width: .06rem;
    height: .1rem;
    position: absolute;
    top: .3rem;
    right: .13rem;
  }
  .shopBar .shopLogo{
    display: flex;
    height: .73rem;
    margin: .2rem .13rem;
  }
  .shopBar .shopLogo .logoimgg{
    width: .36rem;
    height: .36rem;
    margin-top: .2rem;
  }
  .shopBar .shopLogo .shopInfo{
    display: flex;
    flex-direction: column;
    justify-content: left;
    height: .75rem;
    margin-left: .06rem;
    margin-top: .2rem;
  }
  .shopBar .shopLogo .shopInfo .row{
    display: flex;
    align-items: center;
    color: #8d8d8d;
    font-size: .11rem;
  }
  .shopBar .shopLogo .shopInfo .row img{
    width: .17rem;
    height: .12rem;
  }
  .shopBar .shopLogo .shopInfo .limitTime{
    width: 1.37rem;
    height: .18rem;
    line-height: .18rem;
    background-color: #313330;
    color: #fff;
    font-size: .09rem;
    text-align: center;
    display: flex;
    align-items: center;
    margin-top: .04rem;
    padding: 0 .08rem 0 .1rem;
  }
  .shopBar .shopLogo .shopInfo .limitTime img{
    width: .07rem;
    height: .05rem;
    margin-left: .05rem;
  }
  .clearfix{
    clear: both;
    height: 0;
    overflow: hidden;
  }
  .reconmend{
    width: 100%;
    height: .4rem;
    line-height: .4rem;
    text-align: left;
    margin-left: .13rem;
    color: #121212;
    font-size: .13rem;
  }
</style>



