<template>
  <header>
    <div class="header-top">
      <div class="head-search">
        <img class="search" src="../../../static/img/search.png" alt="">
        <input class="input" type="text" placeholder="搜索 （例如：科颜氏）">
      </div>
    </div>
  </header>
</template>

<script>
    export default {
        name: "FindHeader"
    }
</script>

<style scoped>
  /*header*/
  header{
    width: 100%;
  }
  .head-search{
    position: relative;
    display: flex;
    margin:.15rem 0 .1rem .15rem;
  }
  .head-search img{
    width: 0.14rem;
    height: 0.14rem;
    position: absolute;
    top: .09rem;
    left: .08rem;
  }
  .head-search .input{
    width: 3.6rem;
    height: .3rem;
    background-color: #fff;
    border-radius: 0.08rem;
    text-indent: .30rem;
  }
</style>



