<template>
  <!-- Swiper -->
  <div class="bigSlider">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" :key="index" v-for="(p,index) in imgs"><img :src="p" alt=""></div>
      </div>
      <!-- Add Pagination -->
      <div class="swiper-pagination"></div>
      <div class="clearfix"></div>
    </div>
  </div>
</template>

<script>
  import SwiperCss from "../../../node_modules/swiper/dist/css/swiper.css"
  import $ from "jquery"
  import Swiper from "swiper"
    export default {
        props:["menuSlider"],
        name: "IndexBigSlider",
        components:{SwiperCss},
      data(){
          return{
            imgs:[
              "../../../static/img/s1.jpg","../../../static/img/s2.jpg","../../../static/img/s3.jpg",
              "../../../static/img/s4.jpg","../../../static/img/s3.jpg"
            ]
          }
      },
        mounted() {
          var swiper = new Swiper('.swiper-container', {
            pagination: {
              el: '.swiper-pagination',
            },
            autoplay:true,
            loop:true,
            clickable:true
          });
        }
    }
</script>

<style scoped>
  /*swiper*/
  .bigSlider{
    width: 100%;
    height: 1.32rem;
  }
  .swiper-container {
    width: 100%;
    height: 1.32rem;
  }
  .clearfix{
    clear: both;
    height: 0;
    overflow: hidden;
  }
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  .swiper-slide  img {
    width: 100%;
  }
  .bigSlider .swiper-pagination-bullet{
      display: inline-block;
      width: .1rem;
      height: .1rem;
    }
  .bigSlider .swiper-pagination-bullet-active{
      background: #cccccc;
  }
  .swiper-container-horizontal > .swiper-pagination-bullets{
    bottom: -.2rem!important;
  }
  .swiper-container-horizontal > .swiper-pagination-bullets span{
    width: .12rem!important;
    height: .12rem!important;
    background: #f6f6f6!important;
  }
</style>



