import {CARTURL} from "../commons/AjaxURLSchema"
export  default {
  /**
   * 获取carts信息
   * @param cb  返回的数据
   */
  getShopCartInfo(cb){
        fetch(CARTURL).then(res=>{
              res.json().then(data=>{
                  cb(data);
              })
        })
  },
  updateShopProductQal(cb){
    fetch(CARTURL).then(res=>{
        res.json().then(data=>{
          cb(data);
        })
    })
  }
}



