<template>
    <div class="box">
      <shop-cart-header :head="ShopCartHeader"></shop-cart-header>
      <shop-cart-bar :cartBar="ShopCartBar"></shop-cart-bar>
      <shop-cart-section :checkStatus="singleCheckedSel" @shopItemChecked="shopCheckAll" :cartsection="ShopCartSection" :add="addNum" :minus="minusNum" v-if="cartInfo.shops" :data="cartInfo.shops"></shop-cart-section>
      <shop-cart-footer @ShopsChecked="shopCartAllChecked" v-if="cartInfo" :data="cartInfo"></shop-cart-footer>
    </div>
</template>

<script>
    import ShopCartsApi from "../apis/ShopCartsApi"
    import ShopCartHeader from "../components/shopCart/ShopCartHeader";
    import ShopCartBar from "../components/shopCart/ShopCartBar";
    import ShopCartSection from "../components/shopCart/ShopCartSection";
    import ShopCartFooter from "../components/shopCart/ShopCartFooter";
    export default {
        name: "shopCart",
        components: {ShopCartFooter, ShopCartSection, ShopCartBar, ShopCartHeader},
        data(){
          return {
              ShopCartHeader:[],
              ShopCartBar:[],
              ShopCartSection:[],
              ShopCartFooter:[],
              totalPrice:0,
              cartInfo:[],
              checked:true
          }
        },
       methods:{
            /**
             * 初始化数据
             */
            _initData(){
              ShopCartsApi.getShopCartInfo(data=>{
                this.cartInfo = data;
                console.log(this.cartInfo);
                this._countCarts(data);
              })
            },
         /**
          * 统计总价格
          * @param cartInfo
          */
          _countCarts(cartInfo){
              let total = 0;
              cartInfo.shops.forEach((shop,sid)=>{
                  shop.products.forEach((product,pid)=>{
                    if(product.checked){
                      total += product.goodsPrice * product.goodsNum;
                    }
                  })
              });

           this.totalPrice = total;
          },

         /**
          * 根据店铺的下标来改变当前店铺中的商品的选中状态
          */
         shopCheckAll(i){
            console.log(i);
             let isSelected = this.cartInfo.shops[i].checked;
             this.cartInfo.shops[i].products.forEach((product,index)=>{
               product.checked = isSelected;
             })
         },
         /**
          * 店铺的全选
          */
         shopCartAllChecked(){
           let checked = this.cartInfo.checked;
           this.cartInfo.shops.forEach((shop,sid)=>{
                shop.checked = checked;
                shop.products.forEach((product,pid)=>{
                    product.checked = checked;
                })
           })
         },
         /**
          * 根据店铺的编号，商品的编号来修改商品的数量
          * @param pid
          */
          addNum(sid,pid){
                if(this.cartInfo.shops[sid].products[pid]){
                  this.cartInfo.shops[sid].products[pid].goodsNum++;
                }
          },

          minusNum(sid,pid){
              if(this.cartInfo.shops[sid].products[pid].goodsNum <= 1){
                  this.cartInfo.shops[sid].products[pid].goodsNum = 1
              }else{
                  this.cartInfo.shops[sid].products[pid].goodsNum--;
              }
          },
           singleCheckedSel(sid,pid){
            if(this.cartInfo.shops[sid].products[pid]){
                // let isSelected = this.cartInfo.shops[sid].products[pid].checked;
              let isSelected = this.cartInfo.shops[sid].products[pid].checked;
                this.cartInfo.shops[sid].checked = isSelected;
                this.cartInfo.checked = isSelected;
            }

           }
       },
      created(){
        fetch("data/shopcart.json").then((res)=>{
          res.json().then((data)=>{
            this.ShopCartHeader= data;
            this.ShopCartBar= data.shopCartBar;
            this.ShopCartSection= data;
          })
        });

        this._initData();
      },
      mounted(){

      }
    }
</script>

<style scoped>

.box{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
html,body{
  height: 100%;
}
</style>



