<template>
  <!--container-->
  <div class="container">
        <merchant-banner></merchant-banner>
        <merchant-section :mSec="mCon"></merchant-section>
  </div>
</template>

<script>
    import MerchantBanner from "./merchantBanner";
    import MerchantSection from "./merchantSection";
    export default {
        name: "merchantContainer",
        components: {MerchantSection, MerchantBanner},
        props:["mCon"]
    }
</script>

<style scoped>
  .container{
    flex: 1;
    overflow-x: hidden;
    overflow-y: auto;
  }
</style>



