</template>

<script>
    import IndexSectionTop from "./IndexSectionTop";
    import IndexSectionContainer from "./IndexSectionContainer";
    export default {
      props:["menu2"],
      name: "IndexSection",
      components: {IndexSectionContainer, IndexSectionTop}
    }
</script>

<style scoped>
  /*section*/
  section{
    width: 100%;
    background: #fefefe;
  }
  .section{
    width: 3.46rem;
    margin: 0 auto;
  }
</style>



