<template>
    <div class="box">
        <goods-list-header v-if="GoodsListHeader.headBar != undefined" :head="GoodsListHeader.headBar"></goods-list-header>
        <goods-container :con="GoodsContainer" :content="contents"></goods-container>
        <goods-footer></goods-footer>
    </div>
</template>

<script>
    import GoodsListHeader from "../components/goodsList/GoodsListHeader";
    import GoodsFooter from "../components/goodsList/GoodsFooter";
    import GoodsContainer from "../components/goodsList/GoodsContainer";
    export default {
        name: "goodsList",
        components: {
          GoodsContainer,
          GoodsFooter,
          GoodsListHeader
        },
        data(){
            return {
              GoodsListHeader:[],
              GoodsContainer:[],
              GoodsFooter:[],
              contents:[]
            }
        },
        mounted(){
            let index=this.$route.params.id;
            console.log(index);
            fetch("http://localhost:3001").then(res => {
              res.json().then(data => {
                this.contents = data.clothing.goodsMenu1[index];
              })
            })
        },
        created(){
            fetch("data/goodslistdata.json").then((res)=>{
                res.json().then((data)=>{
                    this.GoodsListHeader= data;
                    this.GoodsContainer= data;
                    this.GoodsFooter= data;
                })
            });
        }
    }
</script>

<style scoped>
  .box{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  html,body{
    height: 100%;
    overflow-x: hidden!important;
  }
</style>



