<template>
  <!--banner-->
  <div class="banner">
    <div class="bar">
      <!-- Swiper -->
      <div class="bigSlider">
        <div class="swiper-container Slider">
          <div class="swiper-wrapper ">
            <div class="swiper-slide" :key="index" v-for="(s,index) in slider"><img :src="s" alt=""></div>
          </div>
          <!-- Add Pagination -->
          <div class="swiper-pagination"></div>
          <div class="clearfix"></div>
        </div>
        <div class="merchant">{{allmerchant}}</div>
      </div>
    </div>
  </div>
</template>

<script>
  import SwiperCss from "../../../node_modules/swiper/dist/css/swiper.css"
  import $ from  "jquery"
  import  Swiper from "swiper"
    export default {
        name: "merchantBanner",
        components:{SwiperCss},
        data(){
          return{
              slider:[ "../../../static/img/slide1.jpg",
                "../../../static/img/slide2.jpg",
                "../../../static/img/slide3.jpg"],
              allmerchant:"全部商家"
          }
        },
        mounted() {
          // 轮播图
          var swiper = new Swiper('.swiper-container', {
            pagination: {
              el: '.swiper-pagination',
            },
            autoplay:true,
            loop:true,
            clickable:true
          });
        }
    }
</script>

<style scoped>
  /*banner*/
  .banner{
    width: 100%;
    margin: 0 auto 0;
    padding-bottom: .3rem;
  }

  .bar{
    width: 3.43rem;
    height: 1.76rem;
  }
  .bar .Slider{
    width: 3.43rem;
    height: 1.76rem;
    border-radius: 5px;
    margin-left: .2rem;
  }
  .bar .swiper-container .swiper-wrapper img{
    width: 100%;
    height: 100%;
  }
  .swiper-pagination-bullet-active{
    background: #cccccc;
  }
  .swiper-container > .swiper-pagination-bullets{
    bottom: -.2rem!important;
  }
  .swiper-container > .swiper-pagination-bullets span{
    width: .12rem!important;
    height: .12rem!important;
    background: #eeeeee!important;
  }
  .bigSlider .merchant{
    width: 100%;
    margin: .1rem auto;
    color: #141414;
    font-size: .14rem;
    text-align: center;
  }
</style>



