<template>
    <div class="box">
      <!--全选-->
      <div class="checkBox" v-if="data.shops">
        <!--<img src="../../assets/img/checked.jpg" alt="">-->
        <input class="ins" type="checkbox" v-model="data.checked" @change="ShopAllChecked">
        <div class="checkedAll">{{checkAll}}</div>
        <div class="compute">
          <p class="save">{{save}}<span>-${{saveSpan}}</span></p>
          <p class="addAll">
            <span class="add1">合计：￥{{data | goodsCard}}</span>
            <span class="add2">(${{data | totalPriceFilter}})</span>
          </p>
        </div>
        <div class="checkout">{{checkout}}<span>({{data | totalNum}})</span></div>
      </div>
      <!--footer-->
      <footer>
        <ul class="footer">
          <li class="active"><router-link to="/"><img src="../../assets/img/f1.png" alt="">人气</router-link></li>
          <li><router-link to="/find"><img src="../../assets/img/f2.png" alt="">发现</router-link></li>
          <li><router-link to="/merchant"><img src="../../assets/img/f3.png" alt="">商家</router-link></li>
          <li><router-link to="/shopCart"><img src="../../assets/img/ff4.png" alt="">购物袋</router-link></li>
          <li><router-link to="/myHome"><img src="../../assets/img/f5.png" alt="">我的</router-link></li>
        </ul>
      </footer>
    </div>
</template>

<script>
    export default {
        name: "ShopCartFooter",
        props:["data"],
        data(){
          return {
             "checkAll":"全选",
             "save":"共节省：",
             "saveSpan":"3.69",
             "addAll1":"330.47",
             "addAll2":"47.00",
             "checkout":"去结算",
             "spanNum":"(4)",
              "checked":true
          }
        },
        filters:{
            totalPriceFilter(cartInfo){
            let total = 0;
            if(cartInfo.shops){
              cartInfo.shops.forEach((shop,sid)=>{
                shop.products.forEach((product,pid)=>{
                  if(product.checked){
                    total += product.goodsPrice * product.goodsNum;
                  }
                })
              })
            }
            // total = total  + 18.69 - 3.69
            return parseFloat(total).toFixed(2);
          },
          goodsCard(cartInfo){
            let result = 0;
            if(cartInfo.shops){
              cartInfo.shops.forEach((shop,sid)=>{
                shop.products.forEach((product,pid)=>{
                  if(product.checked){
                    result += product.goodsPrice * product.goodsNum;
                  }
                })
              })
            }
            // result = result  + 18.69 - 3.69;
            return  result = parseFloat((result)*6.6).toFixed(2);
          },
          totalNum(cartInfo){
              let sum = 0;
              if(cartInfo.shops){
                cartInfo.shops.forEach((shop,sid)=>{
                  shop.products.forEach((product,pid)=>{
                    if(product.checked){
                      sum += parseInt(product.goodsNum);
                    }
                  })
                })
              }
              return sum;
          }
        },
        methods:{
          ShopAllChecked(){
            this.$emit("ShopsChecked")
          }
        }

    }
</script>

<style scoped>
  @import "../../assets/css/shopCartCommon.css";
  .box{
    height: 1.06rem;
  }
  .addAll{
    font-weight: 600!important;
  }
  .ins[type=checkbox] {
    cursor: pointer;
    font-size: .21rem;
    width: .05rem;
    height: .05rem;
    border-radius: 50%;
    position: relative;
    margin-right: .1rem;
  }

  .ins[type=checkbox]:after {
    position: absolute;
    width: .22rem;
    height: .22rem;
    top: -0.07rem;
    left: -0.03rem;
    content: " ";
    background-color: #eee;
    color: #fff;
    font-weight: 600;
    border: none;
    display: inline-block;
    visibility: visible;
    text-align: center;
    box-sizing: border-box;
    padding-top: .03rem;
    font-size: .21rem;
    border-radius: 50%;
  }

  .ins[type=checkbox]:checked:after {
    content: "√";
    font-size: .16rem;
    background-color: #302131;
    text-align: center;
    text-indent: -.05rem;
  }
</style>



