<template>
  <div class="container">
    <index-big-slider :menuSlider="menuSlider"></index-big-slider>
    <index-icons :menu1="menu"></index-icons>
    <index-section :menu2="clotheslist"></index-section>
    <index-hot-menu></index-hot-menu>
    <index-activity v-if="menuTit != undefined" :menu3="menuTit"></index-activity>
    <index-clothing :menu4="clotheslist"></index-clothing>
  </div>
</template>

<script>
    import IndexBigSlider from "./IndexBigSlider";
    import IndexIcons from "./IndexIcons";
    import IndexSection from "./IndexSection";
    import IndexHotMenu from "./IndexHotMenu";
    import IndexActivity from "./IndexActivity";
    import IndexClothing from "./IndexClothing";
    export default {
        props:["menuSlider","menu","menuTit","clotheslist"],
        name: "IndexContainer",
        components: {IndexClothing, IndexActivity, IndexHotMenu, IndexSection, IndexIcons, IndexBigSlider}
    }
</script>

<style scoped>
.container{
  flex: 1;
  overflow-x: hidden;
  overflow-y: auto;
}
</style>



