<template>
    <div class="in">
      <index-header :head="data.menuList" :head2="data.icon" :head3="data.icon1" ></index-header>
      <index-container v-if="data.idxBigSlider != undefined" :menuSlider="data.idxBigSlider" :menu="data.icons" :menuTit="data.title" :clotheslist="data.clothing"></index-container>
      <index-footer></index-footer>
    </div>
</template>

<script>
  import  $ from "jquery";
    import publicCss from "../assets/css/public.css";
    import IndexHeader from "../components/index/IndexHeader";
    import IndexBigSlider from "../components/index/IndexBigSlider";
    import IndexContainer from "../components/index/IndexContainer";
    import IndexIcons from "../components/index/IndexIcons";
  import IndexFooter from "../components/index/IndexFooter";
    export default {
        name: "index",
      data(){
        return {
          data:{}
        }
      },
      created(){
          fetch("data/indexdata.json").then((res)=>{
            res.json().then((data)=>{
                this.data= data
            })
          })
      },
      components: {IndexFooter, IndexIcons, IndexContainer, IndexBigSlider, IndexHeader,publicCss},
      mounted() {
        $(".menu li").each(function (i) {
          $(this).on("click",function (i) {
            if($(this).className != "active"){
              $(this).addClass("active");
            }
            if($(this).className == "active"){
              $(this).removeClass("active");
            }
          })
        }),


        //发现页面的选项卡
        $(".onFocus li a").on("click",function () {
          $(this).css("backgroundColor","#fff");
        });
        $(document).on("click",function () {
          $(this).css("backgroundColor","#f7f7f7");
        });
        $(".onFocus li a").on("click",function(event){
          event.stopPropagation();
        });
      }
    }
</script>

<style scoped>
  .in{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  html,body{
    height: 100%;
  }
</style>



