<template>
  <div class="content">
    <div class="con">
      <div class="con-left">
        <img :src="secConBot.img0" alt="">
      </div>
      <div class="con-right">
        <ul>
          <li>{{secConBot.rightLi}}</li>
          <li><p>{{secConBot.rightP}}</p><code class="code">${{secConBot.code1}}</code></li>
          <li><p>{{secConBot.youhui}}</p><code class="code">-${{secConBot.code2}}</code></li>
        </ul>
      </div>
    </div>
    <div class="con-sec">
      <div class="con-left"><img  :src="secConBot.img1" alt=""></div>
      <div class="con-sec-right con-right">
        <div class="sec-top">
          <div class="txt">{{secConBot.txt}}</div>
          <span>{{secConBot.spanCon}}<img :src="secConBot.img3" alt=""></span>
        </div>
        <div class="sec-bot">
          <div class="tit-txt">{{secConBot.titTxt}}<img :src="secConBot.img4" alt=""></div>
          <span class="price">${{secConBot.price}}</span>
        </div>
      </div>
    </div>
    <div class="con-sec">
      <div class="con-left"><img  :src="secConBot.img5" alt=""></div>
      <div class="con-sec-right con-right">
        <div class="sec-top">
          <div class="txt">{{secConBot.txt2}}</div>
        </div>
        <div class="sec-bot">
          <div class="tit-txt">{{secConBot.titTxt2}}<img :src="secConBot.img6" alt=""></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import ShopCartSecConTop from "./ShopCartSecConTop";
    export default {
        name: "ShopCartSecConBot",
        components: {ShopCartSecConTop},
        props:["secConBot"]
    }
</script>

<style scoped>
  @import "../../assets/css/shopCartCommon.css";
</style>



