import Vue from 'vue'
import Router from 'vue-router'

import index from '../pages/index'
import find from  '../pages/find'
import merchant from  '../pages/merchant'
import shopCart from  '../pages/shopCart'
import myHome from  '../pages/myHome'
import fashionShoes from  '../pages/fashionShoes'
import goodsList from  '../pages/goodsList'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',name: 'index',component: index
    },
    {
      path: '/find', name: 'find',component: find
    },
    {
      path: '/merchant',name: 'merchant',component: merchant
    },
    {
      path: '/shopCart',name: 'shopCart',component: shopCart
    },
    {
      path: '/myHome',name: 'myHome',component: myHome
    },
    {
      path: '/fashionShoes',name: 'fashionShoes',component: fashionShoes
    },
    {
      path: '/goodsList/:id',name: 'goodsList',component: goodsList
    }
  ]
})



