<template>
  <!--header-->
  <header>
    <div class="header">
      <div class="title">{{head.headTit}}</div>
      <p class="manage">{{head.manage}}</p>
    </div>
  </header>
</template>

<script>
    export default {
        name: "ShopCartHeader",
        props:["head"]
    }
</script>

<style scoped>
  /*header*/
  html,body{
    width: 100%;
  }
  header {
    width: 100%;
    display: flex;
  }
  .header{
    width: 100%;
    height: .5rem;
    float: left;
    border-bottom: 1px solid #c5c6c6;
  }
  .header .title{
    text-align: center;
    height: .5rem;
    line-height: .5rem;
    color: #000;
    font-size: .15rem;
    margin-left: 1.5rem;
    float: left;
  }
  .header .manage{
    float: right;
    color: #181818;
    font-size: .17rem;
    height: .5rem;
    line-height: .5rem;
    margin-right: .17rem;
  }
</style>



