<template>
  <!-- Swiper -->
  <div class="slider">
    <div class="swiper-container small_slider1">
      <div class="swiper-wrapper">
        <div class="swiper-slide" :key="index" v-if="imgs" v-for="(p,index) in imgs"><a href="javascript:;"><img :src="p" alt=""></a></div>
      </div>
    </div>
    <div class="price">$75减$12</div>
  </div>
</template>

<script>
  import SwiperCss from "../../../node_modules/swiper/dist/css/swiper.css"
  import $ from "jquery"
  import Swiper from "swiper"
    export default {
        name: "IndexSecConSmallSlider",
        components:{SwiperCss},
        data(){
          return{
            imgs:[
              "../../../static/img/small_silde1.jpg","../../../static/img/small_slide2.jpg","../../../static/img/small_slide3.jpg",
              "../../../static/img/small_slide4.jpg","../../../static/img/small_slide5.jpg"
            ]
          }
        },
        mounted(){
          //小轮播图
          var swiper2 = new Swiper('.small_slider1',{
            pagination: {
              el: '.swiper-pagination',
            },
            autoplay:true,
            loop:true,
            clickable:true
          });
        }

    }
</script>

<style scoped>
 .slider{
    width: .87rem;
    height: .75rem;
    background: #fff;
    margin: .05rem auto 0;
  }
 .slider .small_slider1{
    width: .46rem;
    height: .43rem;
    padding-top: .07rem;
  }
  .imgLeft .slider .small_slider1 img{
    width: .46rem;
    height: .43rem;
    border-radius: 50%;
  }
  .imgLeft .slider .small_slider1 span{
    display: none;
  }
 .slider .price{
    color: #b72947;
    font-size: .06rem;
    text-align: center;
    margin-top: .04rem;
  }
</style>



