<template>
    <div class="container">
       <fashion-big-slider :slider="fashionCon"></fashion-big-slider>
        <fashion-con-list :conList="fashionCon"></fashion-con-list>
    </div>
</template>

<script>
    import FashionBigSlider from "./FashionBigSlider";
    import FashionConList from "./FashionConList";
    export default {
        name: "FashionContainer",
        components: {FashionConList, FashionBigSlider},
        props:["fashionCon"]
    }
</script>

<style scoped>
  .container{
    flex: 1;
    overflow-x: hidden;
    overflow-y: auto;
  }
</style>



