<template>
  <div class="bar">
    <ul class="head-bar">
      <li :key="index" v-for="(lis,index) in cartBar"><a href="">{{lis}}</a></li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "ShopCartBar",
        props:["cartBar"]
    }
</script>

<style scoped>
  .bar{
    width: 100%;
    float: left;
  }
  .bar .head-bar{
    line-height: .35rem;
    overflow-x: auto;
    white-space: nowrap;
    border-bottom: 1px solid #e5e5e5;
    margin-top: -.35rem;
  }
  .bar .head-bar li:first-child{
    margin-left: 1.15rem;
  }
  .bar .head-bar::-webkit-scrollbar{
    display: none;
  }
  .bar .head-bar li{
    width: 1.6rem;
    height: .35rem;
    text-align: center;
    display: inline-block;
  }
  .bar .head-bar li a{
    display: block;
    color: #000;
    font-size: .12rem;
  }
  .bar  .head-bar li:first-child{
    border-bottom: 2px solid #000;
  }
</style>



