<template>
    <div class="box">
      <merchant-header :mHead="MerchantHeader"></merchant-header>
      <merchant-container :mCon="MerchantContainer"></merchant-container>
      <merchant-footer></merchant-footer>
    </div>
</template>

<script>
    import MerchantHeader from "../components/merchant/merchantHeader";
    import MerchantContainer from "../components/merchant/merchantContainer";
    import MerchantFooter from "../components/merchant/merchantFooter";
    export default {
        name: "merchant",
        components: {MerchantFooter, MerchantContainer, MerchantHeader},
        data(){
          return {
            MerchantHeader:[],
            MerchantContainer:[]
          }
        },
        created(){
          fetch("data/merchantdata.json").then((res)=>{
            res.json().then((data)=>{
              this.MerchantHeader= data;
              this.MerchantContainer= data;
            })
          })
        }
    }
</script>

<style scoped>
  .box{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  html,body{
    height: 100%;
  }
</style>



