<template>
  <div class="sec-top">
    <div class="title">
      <img :src="secMenu" alt="">{{secMenu5}}<code class="fresh">{{secMenu6}}</code>{{secMenu7}}
      <img :src="secMenu1" alt="">
    </div>
    <div class="redBox">
      <h4>
        {{secMenu3}}
        <a href="javascript:;">
          {{secMenu4}}
          <img :src="secMenu2" alt="">
        </a>
      </h4>
      <span class="spanLeft"></span>
      <span class="spanRight"></span>
    </div>
  </div>
</template>

<script>
    export default {
        props:["secMenu","secMenu1","secMenu2","secMenu3","secMenu4","secMenu5","secMenu6","secMenu7"],
        name: "IndexSectionTop"
    }
</script>

<style scoped>
  .sec-top{
    width: 100%;
    height: .55rem;
    line-hight:.55rem;
    text-align: center;
  }
  .sec-top .title{
    width: 100%;
    font-size: .15rem;
    color: #262626;
    font-weight: normal;
    letter-spacing: 1px;
    margin-top: .24rem;
  }
  .sec-top .title img:first-child{
    margin-right: 5px;
  }
  .sec-top .title img:last-child{
    margin-left: 3px;
  }
  .sec-top .title img{
    width: .21rem;
    height: .18rem;
    vertical-align: middle;
    display: inline-block;
  }
  .sec-top .title .fresh{
    width: .82rem;
    height: .15rem;
    color: #c85454;
    display: inline!important;
  }
  .sec-top  .redBox{
    width: 3rem;
    background: #c9433e;
    height: .17rem;
    line-height: .17rem;
    text-align: center;
    position: relative;
    left: .25rem;
    top: .05rem;
  }
  .sec-top  .redBox h4{
    font-size: .08rem;
    color: #f6f8e7;
    font-weight: normal;
    position: relative;
    border: none;
    letter-spacing: 1px;
  }
  .sec-top  .redBox h4 a{
    color: #f6f8e7;
  }
  .sec-top  .redBox h4 a img{
    width: .05rem;
    height: .09rem;
    vertical-align: middle;
    position: absolute;
    top: 3px;
    right: .25rem;
  }
  .redBox span{
    width: 0;
    height: 0;
    border: 9px solid transparent;
    position: absolute;
  }
  .redBox .spanLeft{
    border-left-color: #ffffff;
    left: -0.01rem;
    top: 0;
  }
  .redBox .spanRight{
    border-right-color: #ffffff;
    right: -0.01rem;
    top: 0;
  }
</style>



