<template>
  <!--section-->
  <section>
      <div class="slider">
        <!-- Swiper -->
        <div class="swiper-container sliderImg">
          <div class="swiper-wrapper">
            <!--<div class="swiper-slide" :key="index" v-for="(s,index) in slide"><img :src="s.img" alt=""></div>-->
            <div class="swiper-slide"><img :src="content.img" alt=""></div>
          </div>
          <!-- Add Pagination -->
          <div class="swiper-pagination"></div>
        </div>
      </div>
      <div class="goodsCon">
        <!--<p class="topTit">{{sec.topTit}}</p>-->
        <!--<p class="secTit">{{sec.secTit}}</p>-->
        <!--<div class="goodsPrice">{{sec.goodsPrice}}</div>-->
        <!--<code class="straight">{{sec.straight}}</code>-->
        <p class="topTit">{{content.tit}}</p>
        <p class="secTit">{{sec.secTit}}</p>
        <div class="goodsPrice">{{content.money}}</div>
        <code class="straight">{{sec.straight}}</code>
      </div>
  </section>
</template>

<script>
  import SwiperCss from "../../../node_modules/swiper/dist/css/swiper.css"
  import $ from  "jquery"
  import  Swiper from "swiper"
    export default {
        name: "GoodsSection",
        props:["sec","content"],
        components:{SwiperCss},
        data(){
          return{
             // "slide": [
             //     {"img":"../../../static/img/list1.jpg"},
             //     {"img":"../../../static/img/list2.jpg"}
             //  ]
          }
        },
        mounted() {
          //商品列表
          var swiper = new Swiper('.swiper-container', {
            autoplay:false
          });
        }
    }
</script>

<style scoped>
  /*section*/
  section{
    width: 100%;
    background-color: #fff;
  }
  .slider{
    width: 3.42rem;
    height: 3.35rem;
    margin-left: .13rem;
  }
  .slider .swiper-container{
    width: 3.42rem;
    height: 3.35rem;
  }
  .slider .swiper-container .swiper-wrapper .swiper-slide img{
    width: 3.42rem;
    height: 3.13rem;
  }
  .swiper-pagination span{
    width: .07rem!important;
    height: .07rem!important;
  }
  .swiper-pagination span{
    background: #000000!important;
  }
  .swiper-container-horizontal > .swiper-pagination-bullets{
    bottom: -.12rem!important;
  }
  .swiper-container-horizontal > .swiper-pagination-bullets span{
    width: .08rem!important;
    height: .08rem!important;
    background: #000000!important;
  }
  .goodsCon{
    width: 3.42rem;
    height: 1.56rem;
    margin-left: .13rem;
    display: flex;
    flex-direction: column;
    justify-content: left;
  }
  .goodsCon .topTit{
    color: #191919;
    font-size: .13rem;
    letter-spacing: 1px;
    line-height: .19rem;
  }
  .goodsCon .secTit{
    color: #c4c4c4;
    font-size: .1rem;
  }
  .goodsCon .goodsPrice{
    color: #1d1d1d;
    font-size: .15rem;
    margin: .22rem 0 .2rem 0;
  }
  .goodsCon .straight{
    width: .75rem;
    height: .19rem;
    line-height: .19rem;
    text-align: center;
    border: 1px solid #c2c2c2;
    background-color: #f8f8f8;
    color: #151515;
    font-size: .1rem;
  }
</style>



