<template>
  <div class="content">
    <div class="con">
      <div class="con-left">
        <img :src="secConTop.img0" alt="">
      </div>
      <div class="con-right">
        <ul>
          <li>{{secConTop.rightLi}}</li>
          <li><p>{{secConTop.rightLi}}</p><code class="code">${{secConTop.code1}}</code></li>
          <li><p>{{secConTop.xiaofei}} <img :src="secConTop.img1" alt=""></p><code class="code">${{secConTop.code2}}</code></li>
        </ul>
      </div>
    </div>
    <div class="con-sec">
      <div class="con-left"><img  :src="secConTop.img2" alt=""></div>
      <div class="con-sec-right con-right">
        <div class="sec-top">
          <div class="txt">{{secConTop.txt}}</div>
          <span>{{secConTop.spanCon}}<img :src="secConTop.img3" alt=""></span>
        </div>
        <div class="sec-bot">
          <div class="tit-txt">{{secConTop.titTxt}}<img :src="secConTop.img4"  alt=""></div>
          <span class="price">${{secConTop.price}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

    export default {
        name: "ShopCartSecConTop",
        props:["secConTop"]
    }
</script>

<style>
     @import "../../assets/css/shopCartCommon.css";
</style>



