<template>
     <div class="box">
       <fashion-header :head="FashionHeader"></fashion-header>
       <fashion-container :fashionCon="FashionContainer"></fashion-container>
       <index-footer></index-footer>
     </div>
</template>

<script>
    import FashionContainer from "../components/fashionShoes/FashionContainer";
    import FashionHeader from "../components/fashionShoes/FashionHeader";
    import IndexFooter from "../components/index/IndexFooter";
    export default {
        name: "fashionShoes",
        components: {IndexFooter, FashionHeader, FashionContainer},
        data(){
          return {
            FashionHeader:[],
            FashionContainer:[]
          }
        },
        created(){
          fetch("data/fashionshoes.json").then((res)=>{
            res.json().then((data)=>{
              this.FashionHeader= data;
              this.FashionContainer= data;
            })
          });
        }
    }
</script>

<style scoped>
  .box{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  html,body{
    height: 100%;
  }
</style>



