<template>
  <!--closing-->
  <div class="closing">
    <div class="goodslist close-goods">
      <h2>{{menu4.hs}}</h2>
      <p><code class="shadow timeout">{{menu4.timeout}}</code>{{menu4.shadow}}</p>
      <!--横向轮播-->
      <div class="side-silder">
        <ul class="goods-menu">
          <li :key="index" v-for="(g,index) in menu4.goodsMenu1">
            <a href="javascript:;">
              <img :src="g.img" alt="">
              <p class="tit">{{g.tit}}</p>
              <p class="money"><code>{{g.money}}</code><code class="disc">{{g.disc}}</code></p>
            </a>
          </li>
        </ul>
        <ul class="goods-menu second">
          <li :key="index" v-for="(m,index) in menu4.goodsMenu2">
            <a href="javascript:;">
              <img :src="m.img" alt="">
              <p class="tit">{{m.tit}}</p>
              <p class="money"><code>{{m.money}}</code><code class="disc">{{m.disc}}</code></p>
            </a>
          </li>
        </ul>
        <ul class="doudou">
          <li class="focus" :key="index" v-for="(d,index) in menu4.doudou"><img :src="d.img" alt=""></li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        props:["menu4"],
        name: "IndexClothing"
    }
</script>

<style scoped>
  .goodslist{
    width: 100%;
    background-color: #fff;
    text-align: center;
    margin-top: .1rem;
    box-shadow:  4px 12px 15px #f0f1f0;
    border-radius: 10px;
    overflow: hidden;
    margin-bottom: .1rem;
  }
  .goodslist h2{
    font-size: .18rem;
    line-height: .26rem;
    color: #2f2f2f;
    margin: .2rem  0 .05rem;
    font-weight: normal;
    letter-spacing: 3px;
    padding-top: .2rem;
  }
  .goodslist p{
    font-size: .12rem;
    color: #6b6b6b;
    letter-spacing: 2px;
    line-height: .15rem;
  }
  .goodslist p .shadow{
    display: inline-block;
    width: .64rem;
    height: .18rem;
    line-height: .18rem;
    color: #9e9e9e;
    background-color: #f3f3f3;
    font-size: .10rem;
    margin-right: .1rem;
  }
  .goods-menu{
    width: 100%;
    background-color: #fff;
    text-align: center;
    margin-top: .3rem;
    overflow: hidden;
  }
  .goods-menu li{
    width: 1.14rem;
    /*height: 1.40rem;*/
    display: inline-block;
    float: left;
    text-align: center;
  }
  .goods-menu li a{
    display: block;
    text-align: center;
    width: 100%;
    height: 1.4rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1;
  }
  .goods-menu li img{
    width: .93rem;
    height: .75rem;
    vertical-align: middle;
  }
  .goods-menu li a .tit{
    color: #767676;
    font-size: .12rem;
    width: .9rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .goods-menu li a .money{
    width: 1rem;
    height: .15rem;
    line-height: .15rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .goods-menu li a .money code{
    color: #1964a1;
    font-size: .10rem;
    margin-left: .1rem;
  }
  .goods-menu li a .money .disc{
    display: inline-block;
    font-size: .10rem;
    color: #a3a3a3;
    font-style: normal;
    text-decoration: line-through;
    margin-right: .35rem;
  }
  .second{
    margin-top: -0.2rem;
  }
  .doudou{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: -.25rem;
  }
  .doudou ul li{
    align-self: flex-start;
  }
  .doudou ul li img{
    width: .1rem;
    height: .1rem;
  }
  .doudou ul li.focus{
    color: #32303c;
  }
  /*clising*/
  .closing{
    width: 100%;
    background-color: #fff;
  }
  .closing .close-goods{
    width: 3.49rem;
    margin-left: .13rem;
    padding-bottom: .1rem;
  }
  .closing .close-goods .timeout{
    width: .3rem;
    height: .18rem;
    color: #fff;
    background-color: #d07641;
    border-radius: 3px;
    text-align: center;
  }
</style>



