<template>
    <div>
      <div class="shop-group-item" :key="index" :sid="index" v-for="(shop,index) in data">
        <div class="shop-name">
          <input type="checkbox"  @change="shopCheck(index)" v-model="shop.checked"
                 class="ins check goods-check shopCheck">
          <span class="shopName">{{shop.shopName}}</span>
          <h4>
            <a href="#"></a>
          </h4>
        </div>
        <ul>
          <shop-cart-sec-top :checkStatus="checkStatus" v-if="shop.products" :add="add" :sid="index" :pid="pid" :minus="minus" :data="shop" :secTop="secTop"></shop-cart-sec-top>
        </ul>
        <div class="shopPrice">
          本店总计：$<span class="shop-total-amount ShopTotal">{{shop | shopCounterFilter}}</span>
        </div>
      </div>
    </div>
</template>

<script>
    import ShopCartSecTop from "./ShopCartSecTop";
    export default {
      name: "shopCartType",
      components: {ShopCartSecTop},
      props:["secTop","data","add","minus","sid","pid","checkStatus"],
      methods:{
        shopCheck(i){
          this.$emit("shopChecked",i)
        }
      },
      filters:{
        // 店铺小计
        shopCounterFilter(shop){
          let total = 0;
          if(shop.products){
            shop.products.forEach((product,index)=>{
              if(product.checked){
                total += product.goodsPrice * product.goodsNum;
              }
            })
          }
          return total;
        }
      }
    }
</script>

<style scoped>
  .shop-group-item{margin-bottom:.05rem;}
  .shop-group-item ul li{border-bottom:1px solid #fff;}
  .shop-group-item ul li:last-child{border-bottom:none;}

  .shop-name{height:.17rem;line-height:.17rem;margin-top: -.17rem;
    position: relative;}
  .shop-name h4{float:left;font-size:.07rem;padding-left:.12rem;margin-left: .14rem;}
  .shop-name .shopName{
    display: inline-block;
    font-size:.14rem;
    position: absolute;
    top: .25rem;
    left: .4rem;
  }
  .shop-name .coupons{float:right;}
  .shop-name .coupons span{display:inline-block;font-size: .14rem}
  .shop-name .coupons em{color:#e0e0e0;font-size: .14rem}
  .shop-info .shop-info-img img{width:100%;height:100%;}
  .shop-info .shop-info-text h4{font-size:.07rem;display:-webkit-box;-webkit-box-orient:vertical;-webkit-line-clamp:2;overflow: hidden;}

  .shop-info .shop-info-text .shop-brief span{display:inline-block;margin-right:.04rem;}
  .shop-info .shop-info-text .shop-arithmetic a{display:inline-block;width:.11rem;height:.11rem;line-height:.11rem;text-align:center;background:#fff;font-size:.08rem;}
  .shopPrice{background:#fff;height:.17rem;line-height:.17rem;padding:.08rem;text-align:right;font-size: .14rem}
  .shopPrice span{color:#000;font-weight: 600;}
  .ins[type=checkbox] {
    cursor: pointer;
    font-size: .21rem;
    width: .05rem;
    height: .05rem;
    border-radius: 50%;
    position: relative;
    margin-right: .1rem;
    left: .1rem;
    top: -.1rem;
  }

  .ins[type=checkbox]:after {
    position: absolute;
    width: .22rem;
    height: .22rem;
    top: -0.1rem;
    left: -0.03rem;
    content: " ";
    background-color: #eee;
    color: #fff;
    font-weight: 600;
    border: none;
    display: inline-block;
    visibility: visible;
    text-align: center;
    box-sizing: border-box;
    padding-top: .03rem;
    font-size: .21rem;
    border-radius: 50%;
  }

  .ins[type=checkbox]:checked:after {
    content: "√";
    font-size: .16rem;
    background-color: #302131;
    text-align: center;
    text-indent: -.05rem;
  }
</style>



