<template>
    <div class="box">
      <div class="sec-top"><a href="">{{secTop.sTop}}</a><img :src="secTop.img1" alt=""></div>
      <div class="goodsMenu" :key="pid" v-for="(d,pid) in data.products">
        <div class="g-left">
          <input class="ins" @change="checkStatus(sid,pid)" type="checkbox" v-model="d.checked">
          <img class="goodsImg" :src="d.goodsImg" alt="">
        </div>
        <div class="g-right">
          <div class="rig-left">
            <div class="goodsInfo">
              <p>{{d.goodsName}}</p>
              <code class="gray">{{d.goodsColor}}</code>
              <div class="send"><img :src="d.sendImg" alt="">{{d.sendWay}}</div>
              <div class="grid">
                <span class="minus" @click.prevent.stop="minus(sid,pid)">-</span>
                <span class="count">{{d.goodsNum}}</span>
                <span class="add" @click.prevent.stop="add(sid,pid)">+</span>
              </div>
            </div>
          </div>
          <div class="botInfo">
            <p class="price">
              <span class="price1">${{d.goodsPrice}}</span>
              <span class="peice2">(约￥{{d.goodsCard}})</span>
            </p>
            <code class="straight">{{d.straight}}</code>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "ShopCartSecTop",
        props:["secTop","data","add","minus","pid","sid","checkStatus"],
        methods:{
          // goodsCheck(i){
          //   this.$emit("goodsChecked",i);
          // }
        }
    }
</script>

<style scoped>
  section .sec-top{
    height: .32rem;
    line-height: .32rem;
    margin-right: .2rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  section .sec-top a{
    color: #414141;
    font-size: .14rem;
    margin-right: .9rem;
  }
  section .sec-top img{
    width: .06rem;
    height: .1rem;
    vertical-align: middle;
    margin-right: -1rem;
  }
  .goodsMenu{
    width: 100%;
    background-color: #fff;
    height: 1.55rem;
    border-bottom: 1px solid #eeeeee;
    display: flex;
  }
  .goodsMenu .g-left{
    width: 1.4rem;
    height: 1.55rem;
    line-height: 1.55rem;
    display: flex;
    align-items: center;
    justify-content: left;
    margin-left: .1rem;
  }
  .goodsMenu .g-left .chiose{
    width: .2rem;
    height: .2rem;
  }
  .goodsMenu .g-left  .goodsImg{
    width: 1.05rem;
    height: 1rem;
  }
  .goodsMenu  .g-right{
    position: relative;
  }
  .rig-left .goodsInfo{
    display: flex;
    flex-direction: column;
    justify-content: left;
  }
  .rig-left .goodsInfo p{
    color: #292929;
    font-size: .14rem;
    line-height: .31rem;
    margin-top: .1rem;
  }
  .rig-left .goodsInfo .gray{
    color: #989898;
    font-size: .14rem;
    line-height: .2rem;
  }
  .rig-left .goodsInfo .send{
    display: flex;
    align-items: center;
    color: #363636;
    font-size: .14rem;
    line-height: .4rem;
  }
  .rig-left .goodsInfo .send img{
    width: .13rem;
    height: .12rem;
    margin-right: .07rem;
  }
  .rig-left .goodsInfo .grid{
    display: flex;
    width: .77rem;
    height: .21rem;
    border-radius: 3px;
    align-items: center;
    justify-content: center;
  }
  .rig-left .goodsInfo .grid span{
    font-size: .1rem;
    color: #2e2e2e;
    height: .21rem;
    line-height: .21rem;
    text-align: center;
    border: 1px solid #d7d7d7;
  }
  .rig-left .goodsInfo .grid .minus{
    color: #b6b6b6;
    width: .23rem;
  }
  .rig-left .goodsInfo .grid .add{
    color: #4b4b4b;
    width: .23rem;
  }
  .rig-left .goodsInfo .grid .count{
    width: .3rem;
    border: 1px solid #a5a5a5;
  }
  .botInfo{
    /*width: .131rem;*/
    height: .62rem;
    align-self: baseline;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: absolute;
    bottom: .15rem;
    right: -.85rem;
  }
  .box .goodsMenu:nth-child(2) .botInfo{
    position: absolute;
    bottom: .15rem;
    right: -.5rem;
  }
  .botInfo .price{
    width: 1.18rem;
    height: .23rem;
    color: #323232;
    font-size: .15rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .botInfo .price span{
    color: #323232;
    font-size: .15rem;
  }
  .botInfo .straight{
    width: .76rem;
    height: .18rem;
    line-height: .18rem;
    text-align: center;
    background-color: #f6f6f6;
    border: 1px solid #7d7d7d;
    border-radius: 3px;
    font-size: .10rem;
    color: #303030;
    margin-left: .35rem;
    margin-top: .05rem;
  }
  .ins[type=checkbox] {
    cursor: pointer;
    font-size: .21rem;
    width: .05rem;
    height: .05rem;
    border-radius: 50%;
    position: relative;
    margin-right: .1rem;
  }

  .ins[type=checkbox]:after {
    position: absolute;
    width: .22rem;
    height: .22rem;
    top: -0.07rem;
    left: -0.03rem;
    content: " ";
    background-color: #eee;
    color: #fff;
    font-weight: 600;
    border: none;
    display: inline-block;
    visibility: visible;
    text-align: center;
    box-sizing: border-box;
    padding-top: .03rem;
    font-size: .21rem;
    border-radius: 50%;
  }

  .ins[type=checkbox]:checked:after {
    content: "√";
    font-size: .16rem;
    background-color: #302131;
    text-align: center;
    text-indent: -.05rem;
  }
</style>



