<template>
  <!--header-->
  <header>
    <div class="header-bar">
      <router-link to="/"><img class="backFontPage" :src="head.img1" alt=""></router-link>
      <div class="goodsName">{{head.goodsName}}</div>
      <img :src="head.img2" alt="">
    </div>
  </header>
</template>

<script>
  export default {
    name: "GoodsListHeader",
    props:["head"]
  }
</script>

<style scoped>
  /*header*/
  header{
    width: 100%;
    height: .56rem;
  }
  header .header-bar{
    width: 100%;
    height: .56rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header-bar .backFontPage{
    width: .1rem!important;
    height: .16rem!important;
    margin-left: .2rem;
  }
  .header-bar img:last-child{
    width: .17rem;
    height: .17rem;
    margin-right: .22rem;
  }
  .header-bar .goodsName{
    color: #030303;
    font-size: .16rem;
  }
</style>



