<template>
  <!-- header -->
  <header>
    <div class="header-top">
      <div class="head-search">
        <img class="search" :src="head.serachImg" alt="">
        <input class="input" type="text" placeholder="搜索 （例如：科颜氏）">
      </div>
      <div class="message">
        <img :src="head.messageImg" alt="">
      </div>
    </div>
    <div class="bar">
      <ul class="menu">
        <li :key="index" v-for="(list,index) in head.menu"><a href="javascript:;">{{list}}</a></li>
      </ul>
    </div>
  </header>
</template>

<script>
    export default {
        name: "FashionHeader",
        props:["head"]
    }
</script>


<style scoped>
  /*header*/
  header{
    width: 100%;
    height: 0.90rem;
    margin-bottom: 0.04rem;
  }
  .header-top{
    display: flex;
    justify-content: space-around;
    margin-top: .2rem;
  }
  .head-search{
    position: relative;
    display: flex;
  }
  .head-search img{
    width: 0.14rem;
    height: 0.14rem;
    position: absolute;
    top: .09rem;
    left: .08rem;
  }
  .head-search .input{
    width: 3.12rem;
    height: .3rem;
    background-color: #fff;
    border-radius: 0.1rem;
    text-indent: .30rem;
  }
  .message img{
    width: .22rem;
    height: .15rem;
    margin-top: .08rem;
    margin-right: .08rem;
  }
  .bar{
    width: 100%;
    height: .41rem;
    line-height: .41rem;
  }
  .menu{
    overflow-x: auto;
    white-space: nowrap;
    display: flex;
    justify-content: space-between;
    margin-left: .08rem;
  }
  .menu::-webkit-scrollbar{
    display: none;
  }
  .menu li{
    width: .44rem;
    height: .41rem;
    line-height: .41rem;
    text-align: center;
    display: inline-block;
    margin-right :-0.7rem;
    padding:  0 .05rem;
  }
  .menu li a{
    display: block;
    color: #121212;
    font-size: .12rem;
  }
  .menu li:nth-child(2){
    border-bottom: 2px solid #000;
  }

</style>



