<template>
  <!--footer-->
  <footer>
    <ul class="bottom">
      <li class="active"><a href="javascript:;"><img src="../../assets/img/kf.png" alt=""></a></li>
      <li><a href="javascript:;"><img src="../../assets/img/sc.png" alt=""></a></li>
      <li><a href="javascript:;"><img src="../../assets/img/bbag.jpg" alt=""></a></li>
      <li class="addCart" @click="goCarts" ><router-link to="">加入购物车</router-link></li>
    </ul>
  </footer>
</template>

<script>
    export default {
        name: "GoodsFooter",
        methods:{
          goCarts(){
            this.$router.push("/shopCart")
          }
        }

    }
</script>

<style scoped>
  footer .bottom{
    width: 100%;
    height: .51rem!important;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
  }
  footer .bottom li a img{
    width: .2rem;
    height: .2rem;
  }
  footer .bottom li a img:first-child{
    margin-left: .25rem;
  }
  footer .bottom li a img:nth-child(2){
    margin:0 .34rem 0 .38rem;
  }
  footer .bottom li a img:last-child{
    margin-right: .3rem;
  }
  footer .bottom .addCart{
    width: 1.91rem;
    height: .51rem;
    line-height: .51rem;
    text-align: center;
    background-color: #313330;
    margin: 0!important;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  footer .bottom .addCart a{
    color: #fff;
    font-size: .15rem;
    z-index: 10;
  }
</style>



