<template>
    <div class="box">
      <my-home-header></my-home-header>
      <my-home-container></my-home-container>
      <my-home-footer></my-home-footer>
    </div>
</template>

<script>
    import MyHomeHeader from "../components/myHome/MyHomeHeader";
    import MyHomeContainer from "../components/myHome/MyHomeContainer";
    import MyHomeFooter from "../components/myHome/MyHomeFooter";
    export default {
        name: "myHome",
      components: {MyHomeFooter, MyHomeContainer, MyHomeHeader}
    }
</script>

<style scoped>
  .box{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  html,body{
    height: 100%;
  }
</style>



