<template>
  <!--section-->
  <section>
    <ul>
      <li :key="index" v-for="(data,index) in mSec.secLis">
        <div class="info">
          <img :src="data.img1" alt="">
          <div class="words">
            <div class="left"><img :src="data.img2" alt=""><h2>{{data.h2Con}}</h2></div>
            <div class="vegtables">{{data.con}}</div>
          </div>
        </div>
        <div class="fav">
          <img :src="data.img3" alt="">
          <code class="num">{{data.num}}</code>
        </div>
      </li>
    </ul>
  </section>
</template>

<script>
    export default {
        name: "merchantSection",
        props:["mSec"]
    }
</script>

<style scoped>
  /*section*/
  section{
    width: 100%;
    margin-top: -.1rem;
  }
  section ul{
    display: flex;
    flex-direction: column;
    background-color: #fff;
    margin-top: .2rem;
  }
  section ul li{
    display: flex;
    height: .73rem;
    line-height: .73rem;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    width: 100%;
    border-top: 2px solid #eeeeee;
  }
  section ul li .info{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  section ul li .info img{
    width: .55rem;
    height: .55rem;
    vertical-align: middle;
    margin: 0 .12rem 0 .07rem;
  }
  section ul li .info  .words .left,section ul li .info  .words div{
    display: flex;
    height: .22rem;
    justify-content:left;
    align-items: center;
    color: #8d8d8d;
    font-size: .09rem;
  }
  section ul li .info  .words div img{
    width: .17rem;
    height: .12rem;
    margin-right: .08rem;
  }
  section ul li .info  .words div  h2{
    font-size: .09rem;
    color: #191919;
    font-weight: normal;
  }
  section ul li .info  .words .vegtables{
    float: left;
  }
  section ul li .fav{
    position: relative;
  }
  section ul li .fav img{
    width: .19rem;
    height: .17rem;
    position: absolute;
    bottom: -.26rem;
    right: .50rem;
  }
  section ul li .fav .num{
    color: #838383;
    font-size: .05rem;
    position: absolute;
    bottom: -0.55rem;
    left: -.4rem;
  }

</style>



