<template>
  <div class="bigShopList">
    <div class="Con">
      <ul class="goodsBox" :key="index" v-for="(lis,index) in list">
        <li class="goodsLeft">
          <img :src="lis.img1" alt="">
          <div class="goodsInfo">
            <h2>{{lis.oH2}}</h2>
            <code class="goodsTex">{{lis.goodsTex}}</code>
            <p class="bluePrice">{{lis.bluePrice}}</p>
            <code class="straight">{{lis.straight}}</code>
          </div>
        </li>
        <img class="cart" :src="img2" alt="">
      </ul>
    </div>
  </div>
</template>

<script>
    export default {
        name: "GoodsBigShopList",
        props:["list"],
        data(){
          return {
            "img2":"../../../static/img/addCart.jpg"
          }
        }
    }
</script>

<style scoped>
  .Con{
    width: 100%;
    height: 1.25rem;
    display: flex;
    overflow-x: auto;
    white-space: nowrap;
  }
  .Con::-webkit-scrollbar{
    display: none;
  }
  body,html{
    overflow-x: hidden!important;
  }
  .Con ul{
    width: 3.45rem;
    height: 1.25rem;
    background-color: #fff;
    justify-content: space-between;
    margin-left: .11rem;
    position: relative;
    float: left;
  }
  .Con ul .cart{
    width: .35rem;
    height: .35rem;
    position: absolute;
    top: .2rem;
    right: .16rem;
  }
  .Con ul .goodsLeft{
    width: 3.33rem;
    height: .9rem;
    display: flex;
    align-items: center;
    margin: .2rem 0 0 .14rem;
  }
  .Con ul .goodsLeft img{
    width: .73rem;
    height: .82rem;
  }
  .Con ul .goodsLeft .goodsInfo{
    height: .9rem;
    display: flex;
    flex-direction: column;
    margin-left: .15rem;
  }
  .Con ul .goodsLeft h2{
    font-weight: normal;
    color: #1f1f1f;
    font-size: .1rem;
  }
  .Con ul .goodsLeft .goodsTex{
    font-size: .11rem;
    color: #878787;
    margin:.05rem 0;
  }
  .Con ul .goodsLeft .bluePrice{
    color: #006dd1;
    font-size: .13rem;
  }
  .Con ul .goodsLeft .straight{
    width: .75rem;
    height: .19rem;
    line-height: .19rem;
    text-align: center;
    border: 1px solid #c2c2c2;
    background-color: #f8f8f8;
    color: #151515;
    font-size: .1rem;
    margin-top: .12rem;
  }
</style>



