<template>
    <div class="container">
        <my-home-con-head></my-home-con-head>
        <my-home-con-bar-menu></my-home-con-bar-menu>
        <my-home-section></my-home-section>
    </div>
</template>

<script>
    import MyHomeConHead from "./MyHomeConHead";
    import MyHomeConBarMenu from "./MyHomeConBarMenu";
    import MyHomeSection from "./MyHomeSection";
    export default {
        name: "MyHomeContainer",
      components: {MyHomeSection, MyHomeConBarMenu, MyHomeConHead}
    }
</script>

<style scoped>
  .container{
    flex: 1;
    overflow-x: hidden;
    overflow-y: auto;
  }
</style>



