<template>
  <section>
        <shop-cart-type :checkStatus="checkStatus" @shopChecked="shopCheckedAll" :sid="sid" :add="add" :minus="minus" :data="data" v-if="cartsection.cartSection !=undefined" :secTop="cartsection.cartSection"></shop-cart-type>
        <shop-cart-sec-con-top v-if="cartsection.secConInfo !=undefined" :secConTop="cartsection.secConInfo"></shop-cart-sec-con-top>
        <shop-cart-sec-con-bot v-if="cartsection.secConBot !=undefined" :secConBot="cartsection.secConBot"></shop-cart-sec-con-bot>
  </section>
</template>

<script>
    import ShopCartType from "./shopCartType";
    import ShopCartSecConTop from "./ShopCartSecConTop";
    import ShopCartSecConBot from "./ShopCartSecConBot";
    export default {
        name: "ShopCartSection",
        components: {ShopCartSecConBot, ShopCartSecConTop,ShopCartType},
        props:["cartsection","add","minus","data","sid","checkStatus"],
        methods:{
          shopCheckedAll(i){
            this.$emit("shopItemChecked",i)
          }
        }
    }
</script>

<style scoped>
  /*section*/
  section{
    width: 100%;
    flex: 1;
    overflow-x: hidden;
    overflow-y: auto;
  }
</style>



