<template>
    <div class="box2">
      <goods-section v-if="con.goodsCon != undefined" :sec="con.goodsCon" :content="content"></goods-section>
      <goods-color-type v-if="con.colorType != undefined" :type="con.colorType"></goods-color-type>
      <goods-shop-bar v-if="con.barCon != undefined" :bar="con.barCon"></goods-shop-bar>
      <goods-big-shop-list v-if="con.listCon != undefined"  :list="con.listCon"></goods-big-shop-list>
      <goods-commnet v-if="con.content2 != undefined"  :conmment="con.content2"></goods-commnet>
    </div>
</template>

<script>
    import GoodsSection from "./GoodsSection";
    import GoodsColorType from "./GoodsColorType";
    import GoodsShopBar from "./GoodsShopBar";
    import GoodsBigShopList from "./GoodsBigShopList";
    import GoodsCommnet from "./GoodsCommnet";
    export default {
        name: "GoodsContainer",
        props:["con","content"],
        components: {GoodsCommnet, GoodsBigShopList, GoodsColorType, GoodsShopBar, GoodsSection}

    }
</script>

<style scoped>
.box2{
  flex: 1;
  overflow-x: hidden;
  overflow-y: auto;
}
</style>



